import { GLSL } from "gl-react";

const Shader1 = {
  frag: GLSL`
  precision mediump int;
  precision mediump float;

  uniform vec4 array;

  void main() {
    gl_FragColor = array;
  }
`,
  vert: GLSL`
  precision mediump int;
  precision mediump float;

  uniform vec4 u_Color;

  void main() {
    gl_FragColor = u_Color;
  }
`,
};

export default Shader1;
